.searchResultsContainer {
  display: flex;
  z-index: 3;
  position: absolute;
  left: 0px;
  background-color: rgb(212, 212, 212);
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  padding: 45px 0px;
  margin-top: 20px;
  border-bottom: 1px solid black;

  & .searchResults__item {
    margin-bottom: 30px;
  }
}
.searchButton {
  background: none;
  border: none;
  outline: none;
  &:hover {
    cursor: pointer;
  }
}

.searchBarContainer {
  position: relative;
  display: flex;
  .searchBar {
    margin: 0px;
  }
  ::placeholder {
    color: rgb(156, 156, 156);
  }
}
.closeSymbol {
  content: "\2715";
  font-size: 24px;
  position: absolute;
  right: 5px;
  top: 0px;
  border: none;
  background: none;
  &:hover {
    cursor: pointer;
  }
  &:active {
    outline: none;
  }
}
.searchResults__Button {
  height: 40px;
  background: none;
}
.searchResults {
  display: flex;
  flex-direction: column;
  text-decoration: none;
  color: black;
  width: 18%;
  margin-bottom: 30px;
  padding: 10px;
  .searchResults__imageContainer {
    text-decoration: none;
    margin-bottom: 5px;
    color: black;
  }

  .searchResults__image {
    width: 100%;
  }
  .searchResults__footer {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-size: 18px;
    position: relative;
    & .searchResults__title {
      width: 90%;
      margin-bottom: 5px;
    }
    .searchResults__subtitle {
      margin-bottom: 5px;
    }
    .searchResults__price {
      width: 10%;
    }
  }
}
