@import "../../../assets/colors/Colors.module.scss";

.catalogue {
  width: 90%;
  margin: 20px auto;
}

.gridContainer {
  display: grid;
  padding: 5px;
  text-align: center;
  grid-template-columns: auto auto auto auto auto auto;
  grid-template-rows: repeat(4, 40vh);
  grid-template-areas:
    "a a b b c c"
    "a a b b c c"
    "d d d e e e"
    "d d d e e e";
}
